@font-face {
  font-family: "";
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/") format("woff2"), url("../fonts/") format("woff");
}
@font-face {
  font-family: "";
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/") format("woff2"), url("../fonts/") format("woff");
}
@font-face {
  font-family: "";
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/") format("woff2"), url("../fonts/") format("woff");
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

label {
  display: inline-block;
}

button {
  border-radius: 0;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

select {
  word-wrap: normal;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=radio],
input[type=checkbox] {
  box-sizing: border-box;
  padding: 0;
}

textarea {
  overflow: auto;
  resize: vertical;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
  appearance: none;
}

[type=search]::-webkit-search-decoration {
  appearance: none;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

html {
  height: 100%;
}

body {
  margin: 0;
  font-family: "Inter", sans-serif;
  color: #000000;
  background: #fafafa;
  border-radius: 25px;
}

:root {
  --c-white: #FFFFFF;
  --c-black: #000000;
  --c-brand: #F14A16;
  --c-accent: #FC9918;
  --c-purple-1: #370665;
  --c-purple-2: #35589A;
  --c-gray-90: #E5E5E5;
  --c-gray-70: #C4C4C4;
  --c-gray-60: #A4A4A4;
  --c-gray-50: #888888;
  --c-dark-1: #070606;
  --bg: var(--c-white);
  --text: var(--c-black);
  --muted: rgba(40, 40, 40, 0.56);
  --surface: #FAFAFA;
  --surface-2: #FFFFFF;
  --border: rgba(0, 0, 0, 0.12);
  --shadow: 0 10px 30px rgba(0, 0, 0, 0.18);
  --g-brand: linear-gradient(135deg, var(--c-purple-1) 0%, var(--c-purple-2) 100%);
  --btn-primary-bg: var(--c-brand);
  --btn-primary-border: var(--c-brand);
  --btn-primary-text: var(--c-white);
  --btn-ghost-bg: transparent;
  --btn-ghost-border: var(--c-brand);
  --btn-ghost-text: var(--c-brand);
}

body.theme-dark {
  --bg: var(--c-black);
  --text: var(--c-white);
  --muted: rgba(255, 255, 255, 0.70);
  --surface: var(--c-dark-1);
  --surface-2: var(--c-dark-1);
  --border: rgba(255, 255, 255, 0.18);
  --shadow: 0 14px 40px rgba(0, 0, 0, 0.55);
  --g-brand: linear-gradient(135deg, #370665 0%, #35589A 100%);
}

body {
  background: var(--bg);
  color: var(--text);
}

.header__wrap {
  box-sizing: border-box;
  width: 320px;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .header__wrap {
    width: 90%;
    min-width: 768px;
    padding-right: 32px;
    padding-left: 32px;
  }
}
@media (min-width: 992px) {
  .header__wrap {
    min-width: 992px;
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (min-width: 1440px) {
  .header__wrap {
    width: 1440px;
  }
}

.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation {
  display: none;
}

.header__button--hidden {
  display: none;
}

.header__actions {
  display: flex;
  align-items: center;
  gap: 40px;
}

.header__burger {
  appearance: none;
  background: none;
  border: 0;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.search__input {
  width: 100%;
  height: 38px;
  border: 2px solid var(--c-gray-90);
  border-radius: 30px;
  background: rgba(255, 255, 255, 0.6);
  font-weight: 400;
  font-size: 16px;
  letter-spacing: 0.04em;
  color: var(--c-gray-50);
  padding-left: 44px;
  box-shadow: 4px 6px 8px 0 rgba(0, 0, 0, 0.25);
  margin-bottom: 30px;
}

body.theme-dark .search__input {
  border-color: rgba(255, 255, 255, 0.18);
  background: rgba(7, 6, 6, 0.7);
  color: rgba(255, 255, 255, 0.7);
}

body.theme-dark .hero__glow--left {
  position: absolute;
  width: 800px;
  height: 800px;
  background: radial-gradient(rgba(83, 89, 167, 0.568627451) 0%, rgba(0, 0, 13, 0) 75%);
  left: -68%;
  top: 32px;
  opacity: 0.5;
}

.search__field {
  display: flex;
  align-items: center;
  margin: 0;
  position: relative;
}

.search__icon {
  position: absolute;
  left: 13px;
  top: 30%;
  transform: translateY(-50%);
  pointer-events: none;
  width: 20px;
}

.theme-toggle {
  position: relative;
  width: 56px;
  height: 28px;
  padding: 2px;
  border: solid 1px #808080;
  border-radius: 999px;
  background: var(--surface-2);
  box-shadow: var(--shadow);
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  cursor: pointer;
}

.theme-toggle::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: var(--c-gray-50);
  transition: transform 0.25s ease;
  z-index: 2;
}

.theme-toggle.is-light::before {
  transform: translateX(0);
}

.theme-toggle.is-dark::before {
  transform: translateX(28px);
}

.theme-toggle__icon {
  position: relative;
  z-index: 1;
  display: grid;
  place-items: center;
}

.theme-toggle__svg {
  display: block;
}

.hero__text--desktop {
  display: none;
}

.hero__title {
  margin: 0;
  font-weight: 700;
  font-size: 42px;
  line-height: 107%;
  letter-spacing: 0.04em;
  color: var(--text);
  margin-bottom: 15px;
}

.hero__title span {
  color: var(--c-brand);
}

.hero__text {
  font-size: 13px;
  line-height: 156%;
  letter-spacing: 0.02em;
  color: var(--muted);
  padding-right: 19px;
  margin-bottom: 47px;
}

.hero__buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 115px;
  gap: 18px;
}

.btn--primary {
  width: 160px;
  background: var(--btn-primary-bg);
  border-radius: 20px;
  text-decoration: none;
  text-align: center;
  font-weight: 700;
  font-size: 17px;
  letter-spacing: 0.04em;
  color: var(--btn-primary-text);
  padding: 13px 0;
  border: 2px solid var(--btn-primary-border);
  box-shadow: 1px 4px 6px rgba(0, 0, 0, 0.25);
}

.btn--secondary {
  background: rgba(7, 6, 6, 0);
  border: 3px solid var(--c-brand);
  border-radius: 20px;
  padding: 13px 0;
  font-weight: 700;
  font-size: 15px;
  letter-spacing: 0.04em;
  color: var(--c-brand);
  text-decoration: none;
  text-align: center;
}

.btn--primary,
.btn--secondary {
  width: 200px;
}

.btn--with-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn__icon {
  width: 18px;
  margin-right: 7px;
}

.hero__offers {
  display: flex;
  gap: 20px;
}

.offer-card {
  position: relative;
}

.offer-card__img {
  position: absolute;
  top: -65px;
  left: 50%;
  transform: translateX(-50%);
  width: 107px;
  z-index: 2;
}

.offer-card__body {
  width: 250px;
  height: 128px;
  border-radius: 25px;
  background: rgba(165, 79, 79, 0.38);
  backdrop-filter: blur(30px);
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(255, 255, 255, 0.22);
}

.offer-card__title {
  font-size: 16px;
  color: #fff;
  margin: 64px 0 10px 10px;
}

.offer-card__price {
  font-size: 16px;
  color: var(--c-accent);
  margin-left: 20px;
}

.offer-card__price s {
  font-size: 12px;
  color: #fff;
}

.hero__scroll {
  margin: 18px auto 240px;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 1px solid var(--border);
  display: grid;
  place-items: center;
  box-shadow: var(--shadow);
  cursor: pointer;
}

.hero__scroll-icon {
  width: 10px;
}

.navigation__link {
  text-decoration: none;
  font-weight: 400;
  font-size: 16px;
  letter-spacing: 0.04em;
  color: var(--text);
}

.header__button-login {
  border: 1px solid var(--c-brand);
  border-radius: 15px;
  font-weight: 400;
  font-size: 20px;
  letter-spacing: 0.04em;
  color: var(--c-brand);
  align-items: center;
  text-decoration: none;
  padding: 8px 23px;
}

.hero__glow {
  opacity: 0;
  pointer-events: none;
}

@media (min-width: 992px) {
  .hero__text--mobile {
    display: none;
  }
  .hero__text--desktop {
    display: block;
  }
  .header__burger {
    display: none;
  }
  .navigation {
    display: flex;
    gap: 40px;
  }
  .search {
    display: none;
  }
  .header__button--hidden {
    display: inline-flex;
    align-items: center;
  }
  .hero {
    position: relative;
    display: flex;
    align-items: flex-start;
    padding-top: 100px;
    padding-bottom: 140px;
    margin-bottom: 120px;
    overflow: visible;
    background: none;
  }
  body.theme-dark .hero__glow {
    position: absolute;
    display: block;
  }
  body.theme-dark .hero__glow--left {
    width: 1000px;
    height: 1000px;
    background: radial-gradient(#5359a7 0%, rgba(0, 0, 13, 0) 71%);
    left: -12%;
    top: -107px;
    opacity: 0.5;
  }
  body.theme-dark .hero__glow--right {
    width: 900px;
    height: 900px;
    background: radial-gradient(#370665 0%, rgba(255, 255, 255, 0) 65%);
    left: 46%;
    top: -179px;
    opacity: 0.6;
  }
  .hero::before {
    content: "";
    position: absolute;
    inset: 0;
    background: url("../images/bg-hero.png") no-repeat right center;
    background-size: auto 130%;
    z-index: 0;
    pointer-events: none;
  }
  body.theme-dark .hero::before {
    background-image: url("../images/bg-hero-dark.png");
  }
  .hero::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 55%;
    height: 4%;
    backdrop-filter: blur(18px);
    -webkit-backdrop-filter: blur(18px);
    background: linear-gradient(to top, rgba(255, 255, 255, 0.65), rgba(255, 255, 255, 0));
    z-index: 1;
    pointer-events: none;
  }
  body.theme-dark .hero::after {
    background: linear-gradient(to top, rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0));
  }
  .hero__content {
    max-width: 520px;
    margin-top: 80px;
    position: relative;
    z-index: 2;
  }
  .hero__title {
    font-size: 64px;
  }
  .hero__text {
    font-size: 16px;
  }
  .hero__buttons {
    justify-content: flex-start;
    gap: 20px;
  }
  .hero__offers {
    position: absolute;
    left: 109%;
    bottom: -140px;
    z-index: 3;
  }
  .hero__scroll {
    position: absolute;
    left: 10%;
    top: 90%;
    transform: translateY(-50%);
    margin: 0;
    z-index: 3;
  }
}
body.theme-dark .header__burger {
  color: #fff;
}

body.theme-dark svg {
  fill: #fff;
}

.nav a {
  color: var(--color-primary);
  text-decoration: none;
}
.nav a:hover {
  color: var(--color-hover);
}
.nav a:active {
  color: var(--color-active);
}
.nav a.active {
  color: var(--color-secondary);
}

.main__wrap {
  box-sizing: border-box;
  width: 320px;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .main__wrap {
    width: 90%;
    min-width: 768px;
    padding-right: 32px;
    padding-left: 32px;
  }
}
@media (min-width: 992px) {
  .main__wrap {
    min-width: 992px;
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (min-width: 1440px) {
  .main__wrap {
    width: 1440px;
  }
}

.card__image {
  width: 250px;
  height: 220px;
}

.card {
  margin-right: 30px;
  border: 1px solid var(--border);
  border-radius: 40px;
  overflow: hidden;
  flex-shrink: 0;
  width: 250px;
  background: var(--surface);
  margin-bottom: 50px;
  box-shadow: var(--shadow);
}

.cards {
  display: flex;
  margin-bottom: 100px;
}

.card__title {
  font-weight: 400;
  font-size: 16px;
  letter-spacing: 0.02em;
  color: var(--text);
  padding-left: 16px;
}

.card__feature {
  font-weight: 400;
  font-size: 12px;
  letter-spacing: 0.02em;
  color: var(--text);
  margin-top: 10px;
}

.card__footer {
  padding: 0 8px 18px;
  margin-top: 12px;
}

.card__price {
  display: block;
  margin-bottom: 12px;
  font-size: 22px;
  letter-spacing: 0.02em;
  color: var(--c-accent);
  padding-left: 12px;
}

.card__actions {
  display: flex;
  gap: 12px;
  align-items: center;
}

.card__btn {
  height: 42px;
  border-radius: 999px;
  padding: 0 16px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  text-decoration: none;
  font-weight: 700;
  font-size: 13px;
  line-height: 1;
  white-space: nowrap;
}

.card__btn--ghost {
  background: transparent;
  border: 2px solid var(--c-brand);
  color: var(--c-brand);
}

.card__btn--primary {
  background: var(--c-brand);
  border: 2px solid var(--c-brand);
  color: #fff;
  box-shadow: 0 8px 18px rgba(241, 74, 22, 0.55);
}

.card__btn-icon {
  width: 16px;
  height: 16px;
  display: block;
}

@media (min-width: 992px) {
  .cards {
    gap: 28px;
    overflow: visible;
    flex-wrap: nowrap;
  }
  .card {
    margin-right: 0;
  }
}
.benefits {
  border-radius: 25px;
  padding: 32px 24px;
  background: var(--g-brand);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
  margin-bottom: 150px;
}

.benefits__title {
  text-align: center;
}

.benefit {
  width: 100%;
  max-width: 260px;
  padding-left: 46px;
  margin-bottom: 32px;
  font-size: 18px;
  line-height: 1.3;
  text-align: left;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 28px 28px;
}

.benefit--delivery {
  background-image: url("../images/delivert.png");
  background-repeat: no-repeat;
}

.benefit--verified {
  background-image: url("../images/verified.png");
  background-repeat: no-repeat;
}

.benefit--support {
  background-image: url("../images/support.png");
  background-repeat: no-repeat;
}

.benefit__text:last-child {
  margin-bottom: 0;
}

.offer {
  margin-bottom: 100px;
}

.offer__title {
  margin: 0 0 22px;
  text-align: center;
}

.section__title {
  text-align: center;
  color: var(--text);
  margin-bottom: 50px;
  font-size: 22px;
}

.offer__visual {
  position: relative;
  display: grid;
  place-items: center;
  margin-bottom: 22px;
}

.offer__image {
  width: 260px;
  height: auto;
  display: block;
}

.offer__badge {
  position: absolute;
  top: 12px;
  left: 6px;
  width: 86px;
  height: 86px;
  border-radius: 50%;
  background: var(--c-purple-1);
  color: #fff;
  display: grid;
  place-items: center;
  text-align: center;
  font-weight: 700;
  font-size: 22px;
  line-height: 1.05;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.18);
}

.offer__content {
  text-align: center;
}

.offer__subtitle {
  margin: 0 0 16px;
  font-size: 18px;
  color: var(--text);
  opacity: 0.85;
}

.offer__timer {
  display: flex;
  justify-content: center;
  gap: 18px;
  margin-bottom: 18px;
}

.offer__time {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.offer__timer-value {
  width: 74px;
  height: 58px;
  border-radius: 18px;
  background: var(--c-purple-1);
  color: #fff;
  display: grid;
  place-items: center;
  font-weight: 700;
  font-size: 24px;
  line-height: 1;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.18);
}

.offer__timer-label {
  margin-top: 8px;
  font-size: 12px;
  color: var(--text);
  opacity: 0.7;
}

.offer__buttons {
  display: flex;
  justify-content: center;
  gap: 14px;
  margin-bottom: 26px;
}

.offer__button {
  height: 46px;
  padding: 0 18px;
  border-radius: 20px;
  font-weight: 700;
  font-size: 15px;
  letter-spacing: 0.04em;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.offer__button--ghost {
  background: var(--surface-2);
  border: 2px solid var(--c-brand);
  color: var(--c-brand);
}

.offer__button--primary {
  background: var(--c-brand);
  border: 2px solid var(--c-brand);
  color: #fff;
  box-shadow: 1px 4px 6px rgba(0, 0, 0, 0.25);
}

@media (min-width: 992px) {
  .offer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    column-gap: 40px;
  }
  .offer__visual {
    grid-column: 2;
    margin-bottom: 0;
  }
  .offer__image {
    width: 470px;
  }
}
.accessories {
  padding-top: 28px;
}

.accessories__grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px;
  margin-bottom: 160px;
}

.accessories__item {
  border-radius: 22px;
  overflow: hidden;
  border: 2px solid var(--border);
  box-shadow: var(--shadow);
  background: var(--surface);
}

.accessories__image {
  width: 100%;
  height: 150px;
  display: block;
  object-fit: cover;
  object-position: center;
}

@media (min-width: 992px) {
  .accessories__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 22px;
  }
  .accessories__image {
    width: 100%;
    height: 320px;
    object-fit: cover;
    border-radius: 18px;
  }
}
.promo-cards {
  display: flex;
  gap: 18px;
  margin-bottom: 150px;
}

.promo-cards::-webkit-scrollbar {
  display: none;
}

.promo-card {
  flex: 0 0 250px;
  border-radius: 30px;
  overflow: hidden;
  background: var(--c-purple-1);
  border: 2px solid var(--c-purple-2);
  scroll-snap-align: start;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.35);
}

@media (min-width: 992px) {
  .promo-card {
    flex: 0 0 330px;
    margin: 0 auto;
  }
}
.promo-card__image {
  width: 100%;
  height: 220px;
  object-fit: cover;
  display: block;
}

.promo-card__content {
  padding: 0 0 22px 10px;
  padding-top: 10px;
}

@media (min-width: 992px) {
  .promo-card__content {
    padding: 10px 0 23px 19px;
  }
}
.promo-card__title {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 0.02em;
  color: #fff;
}

.promo-card__features {
  margin: 12px 0 0;
  padding-left: 18px;
}

.promo-card__features li {
  margin-top: 8px;
  font-size: 12px;
  color: #fff;
}

.promo-card__price {
  display: block;
