.footer {
  background: var(--c-purple-1);
}

body.theme-dark .footer {
  background: #1a0630;
}

.footer__wrap {
  display: flex;
  flex-direction: column;
}

.footer__column {
  order: 1;
  display: flex;
  flex-direction: column;
}

.footer__column:nth-of-type(4) {
  order: 3;
}

.footer__brand {
  order: 4;
  margin: 0 auto;
  display: inline-block;
}

.footer__bottom {
  order: 5;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__title {
  font-weight: 700;
  font-size: 19px;
  letter-spacing: 0.02em;
  color: #ffffff;
  margin-top: 30px;
  margin-bottom: 20px;
}

.footer__item {
  font-weight: 400;
  font-size: 14px;
  letter-spacing: 0.02em;
  color: #ffffff;
  text-decoration: none;
  margin-bottom: 20px;
}

.footer__social-container {
  display: flex;
  gap: 25px;
}

.footer__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  margin-bottom: 30px;
}

.footer__input {
  border: 2px solid var(--c-brand);
  border-radius: 20px;
  height: 54px;
  background: transparent;
  font-weight: 400;
  font-size: 20px;
  letter-spacing: 0.03em;
  color: rgba(255, 255, 255, 0.85);
  padding-left: 25px;
}

.footer__input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.footer__button {
  border-radius: 20px;
  width: 200px;
  height: 51px;
  background: var(--c-brand);
  font-weight: 700;
  font-size: 26px;
  letter-spacing: 0.05em;
  color: #ffffff;
  border: 2px solid var(--c-brand);
}

.footer__copy {
  font-weight: 700;
  font-size: 15px;
  letter-spacing: 0.05em;
  color: #fff;
  text-align: center;
  margin: 0;
}

@media (min-width: 992px) {
  .footer__wrap {
    padding-top: 100px;
    display: grid;
    grid-template-columns: 260px 1fr 1fr 1fr;
    grid-template-rows: auto auto auto;
    column-gap: 70px;
    row-gap: 60px;
    align-items: start;
  }
  .footer__brand,
  .footer__column,
  .footer__bottom {
    order: initial;
    margin: 0;
  }
  .footer__brand {
    grid-column: 1;
    grid-row: 1;
    justify-self: start;
    align-self: center;
  }
  .footer__brand img {
    width: 220px;
    height: auto;
    display: block;
  }
  .footer__column:nth-of-type(1) {
    grid-column: 2;
    grid-row: 1;
  }
  .footer__column:nth-of-type(2) {
    grid-column: 3;
    grid-row: 1;
  }
  .footer__column:nth-of-type(3) {
    grid-column: 4;
    grid-row: 1;
    max-width: 360px;
  }
  .footer__column:nth-of-type(4) {
    grid-column: 1/-1;
    grid-row: 2;
    display: grid;
    grid-template-columns: 1fr 420px;
    column-gap: 60px;
    row-gap: 30px;
    align-items: end;
    margin-right: 7px;
  }
  .footer__column:nth-of-type(4) > .footer__social {
    grid-column: 2;
    grid-row: 1/3;
    justify-self: end;
    align-self: end;
  }
  .footer__form {
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 24px;
    margin: 0;
  }
  .footer__input {
    width: 420px;
  }
  .footer__button {
    width: 220px;
    font-size: 22px;
  }
  .footer__title {
    font-size: 26px;
    margin-top: 0;
  }
  .footer__item {
    font-size: 18px;
    margin-bottom: 18px;
  }
  .footer__bottom {
    grid-column: 1/-1;
    grid-row: 3;
    display: grid;
    place-items: center;
    margin-top: 10px;
  }
  .footer__copy {
    font-size: 22px;
    text-align: center;
    margin: 0;
  }
}